label {
  font-size: medium;
  margin-left: 30px;
  text-align: left;
}

.popup {
  background-color: white;
  display: flex;
  flex-direction: column;
}

.drop {
  background-color: white;
  height: 300px;
  width: auto;
  margin: 20px;
}

.drop-inner {
  height: 300px;
  width: auto;
  margin: 15px;
  border: grey dashed 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

button {
  height: 60px;
  width: 125px;
  border-radius: 10px;
}

.hidden {
  display: none;
}
